import {user,defaultCar,carIcon,customerList} from "../../api/user.js"
export default {
	data() {
		return {
			carIcon:carIcon,
			uInfo:{
				user_point:0,
				user_product:0,
				user_coupon:0
			},
			defaultCarInfo:null,
			loadCount:1,
			login:false
		};
	},
	onLoad() { 
		uni.hideShareMenu() 
	},
	onShow() {
		if(uni.getStorageSync('isMobile') != 1){
			this.notLogin()
		} else {
			this.login = true;
			this.getUserInfo()
			this.getDefaultCar()
		}
		
	},
	methods: {
		notLogin(){
			var that = this;
			uni.showModal({ 
				title:'登录提醒',
				content:'您还没有登录，请先登录',
				confirmColor:"#B49A83",
				success(r) {
					if(r.confirm) {
						uni.navigateTo({
							url:'/pages/login/login'
						})
					}else if (r.cancel) {
						that.uInfo = {
							user_point:0,
							user_product:0,
							user_coupon:0
						};
						that.defaultCarInfo = null;
						// uni.removeStorageSync('ssssId');
						// that.getUserInfo();
					}
				}
			})
		},
		getUserInfo(){ 
			if(this.loadCount == 1) {
				uni.showLoading({
					title:"加载中..."
				})
			} 
			user().then(res=>{
				if(this.loadCount == 1) {
					uni.hideLoading()
				}
				if(res.data.code == 0){
					this.loadCount = 2
					this.uInfo = res.data.result.user_info
				}
			})
		},
		getDefaultCar(){
			defaultCar().then(res=>{
				if(res.data.code == 0) {
					this.defaultCarInfo = res.data.result.user_car
				}
			})
		},
		openEquity(index) {
			if(uni.getStorageSync('isMobile') != 1){
				this.notLogin()
				return 
			}
			uni.navigateTo({
				url: '/pages/user/user-equity/user-equity?index=' + index
			})
		},
		switchAccounts() {
			if(uni.getStorageSync('isMobile') != 1){
				this.notLogin()
				return 
			}

			uni.showLoading({
				title:"加载中..."
			})

			customerList().then(res=>{
				uni.hideLoading()
				if(res.data.code == 0){
					if(res.data.result.customers.length>1){
						uni.navigateTo({
							url: '/pages/user/user-switching-accounts/user-switching-accounts?mobile='+ this.uInfo.mobile+'&id='+'10048'
						})
					}else{
						uni.navigateTo({
							url:'/pages/login/login'
						})
					}
					
				}
			})
			
		},
		openPage(p){
			if(uni.getStorageSync('isMobile') != 1){
				this.notLogin()
				return 
			}
			if(p == "car-butler") {
				this.$isNotionObd().then(()=>{
					uni.navigateTo({
						url:`/pages/user/${p}/${p}?carId=${this.defaultCarInfo.id}`
					})
				}) 
			} else {
				if(p == "my-car" && this.uInfo.car_num==0){
					uni.navigateTo({
						url:`/pages/user/add-car/add-car`
					})
				} else {
					uni.navigateTo({
						url:`/pages/user/${p}/${p}`
					})
				} 
			}
			
		},
		// 打开菜单栏
		clickMenu(tag){
			if(tag != 'storeList'){
				if(uni.getStorageSync('isMobile') != 1){
					this.notLogin()
					return 
				} else {
					uni.navigateTo({
						url:`/pages/user/${tag}/${tag}`
					})
				}
			} else {
				uni.navigateTo({
					url:`/pages/home/${tag}/${tag}`
				})
			}
		}
	}
}
